.MovieRow {
    margin-bottom: 30px;
}

.MovieRow h2 {
    margin: 0px 0px 0px 30px;
}

.MovieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}

.MovieRow--list {
    transition: all ease 0.5s;
}

.MovieRow--item {
    display: inline-block;
    width: 150px;
}

.MovieRow--item img {
    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.2s;
}

.MovieRow--item img:hover{
    transform: scale(1);
}

.MovieRow--left,
.MovieRow--right {
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0,0,0,0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}

.MovieRow--left {
    left: 0;
}

.MovieRow--right {
    right: 0;
}

.MovieRow:hover .MovieRow--left{
    opacity: 1;
}

.MovieRow:hover .MovieRow--right{
    opacity: 1;
}

@media (max-width: 760px) {
    .MovieRow--left,
    .MovieRow--right {
        opacity: 1;
    }
}