.feature {
    height: 100vh;
}

.feature--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.feature--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.feature--name {
    font-size: 60px;
    font-weight: bold;
}

.feature--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

.feature--points,
.feature--year,
.feature--seasons {
    display: inline-block;
    margin-right: 15px;
}

.feature--points {
    color: #46d369;
}

.feature--description {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.feature--buttons {
    margin-top: 15px;
}

.feature--watchbutton,
.feature--mylistbutton {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}

.feature--watchbutton:hover,
.feature--mylistbutton:hover {
    opacity: 0.7;
}

.feature--watchbutton {
    background: #FFF;
    color: #000;
}

.feature--mylistbutton {
    background: #333;
    color: #FFF;
}

.feature--genres {
    font-size: 18px;
    margin-top: 15px;
    color: #999;
}

@media (max-width: 760px) {
    .feature {
        height: 90vh;
    }
    .feature--name {
        font-size: 40px;
    }
    .feature--info {
        font-size: 16px;
    }
    .feature--description {
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .feature--watchbutton,
    .feature--mylistbutton {
        font-size: 16px;
    }
    .feature--genres {
        font-size: 14px;
    }
}