@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  color: #FFF;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.lists {
  margin-top: -150px;
}

footer {
  margin: 50px 0;
  text-align: center;
}

.loading {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.MovieRow {
    margin-bottom: 30px;
}

.MovieRow h2 {
    margin: 0px 0px 0px 30px;
}

.MovieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}

.MovieRow--list {
    transition: all ease 0.5s;
}

.MovieRow--item {
    display: inline-block;
    width: 150px;
}

.MovieRow--item img {
    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.2s;
}

.MovieRow--item img:hover{
    transform: scale(1);
}

.MovieRow--left,
.MovieRow--right {
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0,0,0,0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}

.MovieRow--left {
    left: 0;
}

.MovieRow--right {
    right: 0;
}

.MovieRow:hover .MovieRow--left{
    opacity: 1;
}

.MovieRow:hover .MovieRow--right{
    opacity: 1;
}

@media (max-width: 760px) {
    .MovieRow--left,
    .MovieRow--right {
        opacity: 1;
    }
}
.feature {
    height: 100vh;
}

.feature--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.feature--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.feature--name {
    font-size: 60px;
    font-weight: bold;
}

.feature--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

.feature--points,
.feature--year,
.feature--seasons {
    display: inline-block;
    margin-right: 15px;
}

.feature--points {
    color: #46d369;
}

.feature--description {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.feature--buttons {
    margin-top: 15px;
}

.feature--watchbutton,
.feature--mylistbutton {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}

.feature--watchbutton:hover,
.feature--mylistbutton:hover {
    opacity: 0.7;
}

.feature--watchbutton {
    background: #FFF;
    color: #000;
}

.feature--mylistbutton {
    background: #333;
    color: #FFF;
}

.feature--genres {
    font-size: 18px;
    margin-top: 15px;
    color: #999;
}

@media (max-width: 760px) {
    .feature {
        height: 90vh;
    }
    .feature--name {
        font-size: 40px;
    }
    .feature--info {
        font-size: 16px;
    }
    .feature--description {
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .feature--watchbutton,
    .feature--mylistbutton {
        font-size: 16px;
    }
    .feature--genres {
        font-size: 14px;
    }
}
header {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    background: transparent;
    transition: all ease 0.5s;
}

header.black {
    background-color: #141414;
}

.header--logo {
    height: 25px;
}

.header--logo img {
    height: 100%;
}

.header--user {
    height: 35px;
}

.header--user img {
    height: 100%;
    border-radius: 3px;
}
